:root {
  --Gray: rgb(193, 193, 193);
  background-color: rgb(176, 193, 252);
}

body {
  margin: 0;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainElement {
  display: flex;
  flex-direction: column;
}

.mainContent {
  display: flex;
  flex-direction: row;
}

footer {
  text-align: center;
}

.navigation {
  background-color: var(--Gray);
  width: 97%;
  height: 40px;
  text-decoration: none;
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 30px;
  border: 2px solid black;
  align-items: center;
}

.navigation img {
  width: 60px;
  height: 60px;
  border: 2px solid black;
  align-items: center;
}

.locationBar {
  background-color: rgb(249, 220, 246);
  width: 350px;
  height: 700px;
  border: 2px inset rgb(87, 87, 87);
  margin: 0;
  padding: 10px;
  text-align: center;
}

.locationBar img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.welcomeContainer {
  display: flex;
  margin-left: 20px;
  margin-right: 20px;
  width: auto;
  height: auto;
  border: 2px solid black;
  padding: 5px;
  background-color: var(--Gray);
}

.welcomeContainer p {
  margin: 70px;
}

.welcomeContainer h1 {
  margin: 20px;
  text-align: center;
}

.welcomeContainer img {
  width: 450px;
  height: 250px;
  margin-right: 5px;
  border: 2px solid black;
  float: left;
  display: block;
}

.welcomeTextOne p {
  font-size: 20px;
  text-indent: 50px;
}

.welcomeTextTwo {
  font-size: 20px;
  text-indent: 50px;
}
.staffContainer {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  width: 100%;
  margin-right: 20px;
}

.staffContent {
  display: flex;
  margin-bottom: 5px;
  background-color: var(--Gray);
  border: 2px solid black;
  width: 100%;
}

.staffContentOne img {
  height: 200px;
  width: 200px;
  border: 2px solid black;
}

.staffImage img {
  width: 100px;
  height: 100px;
  border: 2px solid black;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
}

.appointmentContainer {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  width: 98%;
  height: auto;
  border: 2px solid black;
  padding: 5px;
  background-color: var(--Gray);
}

.appointmentContainer h1 {
  text-align: center;
}

.bookInfo {
  text-align: center;
}

.appointmentContainer li {
  list-style: none;
  border: 2px inset black;
  width: 200px;
  text-align: center;
  margin-bottom: 5px;
  background-color: rgb(228, 228, 228);
}

.proceduresContainer {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  width: 98%;
  height: auto;
  border: 2px solid black;
  padding: 5px;
  background-color: var(--Gray);
}

.proceduresContainer h1 {
  text-align: center;
}

.proceduresContainer li {
  list-style: none;
  text-align: center;
  margin-left: 0;
}
.proceduresContainer p {
  text-align: center;
}

.proceduresListTwo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.procs {
  display: flex;
  flex-direction: column;
  border: 2px solid rgb(90, 90, 90);
  width: 500px;
  height: 300px;
}

.procTitle {
  float: top;
  text-align: center;
  display: block;
}

.procs h3 {
  display: inline-block;
  text-align: center;
  text-decoration: underline;
}

.procDesc {
  display: flex;
}

.procs img {
  width: 180px;
  height: 180px;
  border: 2px solid black;
}

.procDesc p {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.cost p {
  display: flex;
  flex-direction: column;
  text-align: center;
}
